<template>
  <div class="wrap">
    <div class="map" ref="map" />
    <slot />
    <slot name="sider" class="sider" />
  </div>
</template>

<script>
  export default {
    provide () {
      return {
        getMap: this.getMap
      }
    },
    data () {
      let resolve
      let inited = new Promise(r => resolve = r)
      return {
        inited,
        resolve
      }
    },
    methods: {
      getMap () {
        return this.inited
      }
    },
    mounted () {
      this.map = new TYMap.Map(this.$refs.map, {
        zoom: 15,
        center: new TYMap.Point(106.55, 29.57),
        zooms: [3, 19],
        enableScrollWheelZoom: true
      })
      setTimeout(() => {
        this.map.clearOverlays()
        this.resolve(this.map)
      })
    }
  }
</script>

<style lang="scss" scoped>
  .wrap {
    position: relative;
    height: 100%;

    .map {
      width: 100%;
      height: 100%;
    }

    .sider {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
    }
  }
</style>



