<template>
	<div class="menu-wrapper active" >
        <el-menu
      		default-active="2"
      		class="el-menu-vertical-demo"
	        @open="handleOpen"
	      	@close="handleClose"
	      	background-color="#545c64"
	      	text-color="#fff"
	      	active-text-color="#ffd04b"
	      	:collapse="isCollapse" >

			<!-- <div v-for="(item,ind) in menuList" :key="item.id">
				<el-submenu v-if="!!item.children" :index="String(ind+1)">
					<template slot="title" >
			          	<i :class="item.icon"></i>
			          	<span>{{item.name}}</span>
			        </template>
					<el-menu-item-group v-if="!!item.children">
			          	<el-menu-item v-for="(item1,ind1) in item.children" :key="item.id" :index="'a-'+(ind1+1)">
							
							<router-link :to="item1.link">{{item1.name}}</router-link>
			          	</el-menu-item>
			        </el-menu-item-group>
				</el-submenu>

				<el-menu-item v-else :index="String(ind+1)">
					<i :class="item.icon"></i>
					<router-link :to="item.link">
						{{item.name}}
					</router-link>
				</el-menu-item>
			</div> -->
			<el-menu-item index="0">
				<i class="el-icon-home"></i>
				<router-link to="/">首页</router-link>
			</el-menu-item>

			<el-menu-item index="1">
				<i class="el-icon-search"></i>
				<router-link to="/search">搜索</router-link>
			</el-menu-item>

			<el-submenu index="2">
				<template slot="title" >
		          	<i class="el-icon-setting"></i>
		          	<span>数据查询</span>
		        </template>
				<el-menu-item-group>
		          	<el-menu-item index="2-1">
						<router-link to="/search/imei/ ">IMSI、IMEI</router-link>
		          	</el-menu-item>
		          	<el-menu-item index="2-2">
						<router-link to="/search/mac/">WIFI-MAC</router-link>
		          	</el-menu-item>
		          	<el-menu-item index="2-3">
						<router-link to="/">虚拟身份</router-link>
		          	</el-menu-item>
		          	<el-menu-item index="2-4">
						<router-link to="/search/car/">车牌查询</router-link>
		          	</el-menu-item>
		        </el-menu-item-group>
			</el-submenu>

			<el-submenu index="3">
				<template slot="title" >
		          	<i class="el-icon-menu"></i>
		          	<span>研判工具</span>
		        </template>
				<el-menu-item-group>
					<el-submenu index="3-1">
			          	<template slot="title">同行分析</template>
			          	<el-menu-item index="3-1-1">
							<router-link to="/together/mac">mac同行</router-link>
			          	</el-menu-item>
			          	<el-menu-item index="3-1-2">
							<router-link to="/together/imsi">IMSI、IMEI同行</router-link>
			          	</el-menu-item>
			          	<el-menu-item index="3-1-3">
							<router-link to="/together/car">车辆同行</router-link>
			          	</el-menu-item>
			        </el-submenu>

		          	<el-menu-item index="3-2">
						<router-link to="/">碰撞分析</router-link>
		          	</el-menu-item>
		          	<el-menu-item index="3-3">
						<router-link to="/">关联分析</router-link>
		          	</el-menu-item>
		          	<el-menu-item index="3-4">
						<router-link to="/">集合运算</router-link>
		          	</el-menu-item>
		        </el-menu-item-group>
			</el-submenu>

		</el-menu>
	</div>
</template>
<script>
    export default {
        data () {
           return {
           		isCollapse: false,
           		menuList: [
           			{
           				name: "首页",
           				link: "/",
           				icon: "el-icon-search"
           			},{
           				name: "数据查询",
           				link: "/",
           				icon: "el-icon-setting",
           				children: [
	           				{
	           					name: "IMSI、IMEI",
           						link: "/"	
	           				},{
	           					name: "WIFI-MAC",
           						link: "/"	
	           				},{
	           					name: "虚拟身份",
           						link: "/"
	           				},{
	           					name: "车辆查询",
           						link: "/"	
	           				}
           				]
           			},{
           				name: "研判工具",
           				link: "/",
           				icon: "el-icon-menu",
           				children: [
	           				{
	           					name: "同行分析",
           						link: "/"
	           				},{
	           					name: "碰撞分析",
           						link: "/"	
	           				},{
	           					name: "关联分析",
           						link: "/"	
	           				},{
	           					name: "集合运算",
           						link: "/"	
	           				}
           				]
           			}
           		]
           }
        },
        methods: {
	      	handleOpen(key, keyPath) {
		        console.log(key, keyPath);
	      	},
	      	handleClose(key, keyPath) {
		        console.log(key, keyPath);
	      	}
	    },
	    watches: {

	    }
    }
</script>

<style lang="scss">
	.menu-wrapper {
		width: 240px;
		height: 100%;
		background: #001529;
		float: left;
		ul li {
			&.is-active{
				background: #1890ff!important;
			}
			background: #000c17!important;
			div.el-submenu__title{
				background: #001529!important;
			}
			.el-menu-item-group__title{
				padding: 0;
			}
		}
	}
	.menu-wrapper li a{
		color: white;
		text-decoration: none;
	}
</style>


