<template>
  <div v-show="false" />
</template>

<script>
  import sleep from 'sleep'

  export default {
    props: {
      content: {
        type: [String, HTMLElement],
        default: ''
      },
      width: {
        type: Number,
        default: undefined
      },
      height: {
        type: Number,
        default: undefined
      },
      offset: {
        type: TYMap.Size,
        default: undefined
      },
      point: {
        type: TYMap.Point,
        default: null
      }
    },
    inject: ['getMap'],
    watch: {
      content (nv, ov) {
        if (nv !== ov) {
          this.infoWindow.setContent(nv)
        }
      },
      width (nv, ov) {
        if (nv !== ov) {
          this.infoWindow.setWidth(width)
        }
      },
      height (nv, ov) {
        if (nv !== ov) {
          this.infoWindow.setHeight(width)
        }
      },
      async point (nv, ov) {
        if (nv !== ov) {
          let map = await this.getMap()
          if (nv) {
            map.openInfoWindow(this.infoWindow, nv)
          } else {
            map.closeInfoWindow()
          }
        }
      }
    },
    async created () {
      this.infoWindow = new TYMap.InfoWindow(this.content, {
        width: this.width,
        height: this.height,
        offset: this.offset
      })
      this.infoWindow.addEventListener('close', () => { this.$emit('onClose') })
      if (this.point) {
        let map = await this.getMap()
        map.openInfoWindow(this.infoWindow, this.point)
      }
    },
    async destroyed () {
      let map = await this.getMap()
      map.closeInfoWindow()
    }
  }
</script>



