<template>
	<div class="extra-query" :class="activeClass">
		<div class="extra-query-item" v-for="(item,ind) in listData" :keys="item.id">
			<span class="extra-label">选择地点：</span>
			<input type="text" placeholder="请选择地点" v-model="item.place" v-on:click="choosePlace(ind)" />

			<span class="extra-label">开始时间：</span>
			<el-date-picker
		      	v-model="item.startTime"
		      	type="datetime"
		      	placeholder="选择日期时间">
		    </el-date-picker>
		    <span class="extra-label">结束时间：</span>
			<el-date-picker
		      	v-model="item.endTime"
		      	type="datetime"
		      	placeholder="选择日期时间">
		    </el-date-picker>
		    <small class="el-icon-circle-plus" v-on:click="addRow"></small>
		    <small class="el-icon-remove" v-on:click="deleteRow(ind)"></small>
		</div>
	</div>
</template>
<script>
	export default {
		name: "datelist",
		data() {
			return {

			}
		},
		methods: {
			choosePlace: function(e){
	      		this.$emit("chooseEvents",e);
	      	},

	      	//	点击增加一条记录
	      	addRow: function(){	
	      		var now = new Date(),
	      			last = new Date();
	   				last = last.setTime(now.getTime() - 3600 * 1000 * 24 * 7);
	    		this.listData.push({
    			 	startTime: new Date(last),
                    endTime: now,
                    place: '',
                    deviceId: ''
	    		});
	    	},

	    	//	点击删除一条记录
	    	deleteRow: function(e){		
	    		var max = this.listData.length;
	    		if(max == 1){
	    			this.$message("至少保留一项！")
	    		}else{
	    			for(var i = e;i<max-1;i++){
		    			this.listData[i] = this.listData[i+1];
		    		}
		    		this.listData.pop();
	    		}
	    	},
		},
		props: [
			"activeClass",
			"listData"
		],
		mounted: function(){
			this.addRow();
		}
	}
</script>
<style lang="scss">
	$active-color: #1890ff;

	.extra-query{
		display: none;
		&.active{
			display: block;
		}
		.extra-query-item{
			padding: 10px;
			input{
				width: 230px;
				height: 32px;
				border-radius: 3px;
				border: 1px solid #d2d2d2;
				padding: 0 10px;
				&.el-input__inner{
					padding-left: 30px;
				}
			}
			span.el-input__prefix,span.el-input__suffix{
				top: -3px;
			}
			span.extra-label{
				display: inline-block;
				width: 90px;
            	text-align: right;
			}
			small{
				font-size: 30px;
				color: #aaaaaa;
				display: inline-block;
				position: relative;
				top: 6px;
				left: 20px;
				cursor: pointer;
				&.el-icon-remove:hover{
					color: #f36a6a;
				}
				&.el-icon-circle-plus:hover{
					color: $active-color;
				}
			}
		}	
	}
</style>


