<template>
    <div class="search-wrapper">
        <div class="search-query">
            <div class="basic-query">
                <el-select v-model="value" placeholder="请选择">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                <input type="text" placeholder="请输入内容" v-model="imei" />
                <el-button type="primary" v-on:click="queryInit">查询</el-button>
                <el-button>清空</el-button>
                <span class="search-expand" v-on:click="switchQuery">{{extraSwitchText}} <i :class="extraSwitchIcon"></i></span>
            </div>

            <extra-list
                :activeClass="extraSwitchClass"
                :listData="extraQuery"
                @chooseEvents="showTree">
            </extra-list>
        </div>

        <div class="search-content">
        
            <!-- 选项卡 -->
            <tab-case 
                @handleTab1="changeTimeMode" 
                @handleTab2="changePlaceMode">
            </tab-case> 

            <div class="search-table">
                <div class="search-coperation">
                    <el-button v-for="(item,ind) in coporationBtn" v-on:click="timeThrow(ind)" :key="ind">{{item.name}}</el-button>
                </div>

                <!-- 时间模式 -->
                <table class="default-table time-mode-table" :class="tableMode[0]" cellpadding="0" cellspacing="0" border="0">
                    <thead>
                        <tr>
                            <th><input type="checkbox" v-model="selectList[0]" v-on:change="selectAll(0)" /></th>
                            <th>IMSI</th>
                            <th>IMEI</th>
                            <th>手机号码</th>
                            <th>采集地点</th>
                            <th>采集时间<i class="el-icon-d-caret" v-on:click="tableSort('time')"></i></th>
                            <th>归属地</th>
                            <th>运营商</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in tableData" :keys="item.id">
                            <td><input type="checkbox" v-model="item.check"></td>
                            <td>{{item.IMSI}}</td>
                            <td>{{item.IMEI}}</td>
                            <td>{{item.PHONE}}</td>
                            <td>{{item.ADDRESS}}</td>
                            <td>{{item.CAPTURETIME | dateFormat}}</td>
                            <td>{{item.IMSIADDR}}</td>
                            <td>{{item.YYS}}</td>
                        </tr>
                    </tbody>
                </table>

                <!-- 地点模式 -->
                <table class="default-table place-mode-table" :class="tableMode[1]" cellpadding="0" cellspacing="0" border="0">
                    <thead>
                        <tr>
                            <th><input type="checkbox" v-model="selectList[1]" v-on:change="selectAll(1)" /></th>
                            <th>IMSI/IMEI</th>
                            <th>手机号码</th>
                            <th>采集地点</th>
                            <th>天数<i class="el-icon-d-caret" v-on:click="tableSort('day')"></i></th>
                            <th>出现次数<i class="el-icon-d-caret" v-on:click="tableSort('number')"></i></th>
                            <th>最早时间</th>
                            <th>最晚时间</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item,ind) in tableData1" :class="{'active':ind == index}">
                            <td v-if="item.row>0" :rowspan="item.row"><div><input type="checkbox" v-model="item.check"></div></td>
                            <td v-if="item.row>0" :rowspan="item.row"><div>{{item.imsi||item.imei}}</div></td>
                            <td class="bl">{{item.phone}}</td>
                            <td v-on:click="showTable(item.ADDRESS,ind)" class="pointer">{{item.ADDRESS}}</td>
                            <td>{{item.DAYS}}</td>
                            <td>{{item.NUMBERS}}</td>
                            <td>{{item.EARLYTIME | dateFormat}}</td>
                            <td>{{item.LATETIME | dateFormat}}</td>
                        </tr>
                    </tbody>
                </table>
                <div class="search-coperation bottom-coperation">
                    <el-button v-for="(item,ind) in coporationBtn" v-on:click="timeThrow(ind)" :key="ind">{{item.name}}</el-button>
                </div>
                <el-pagination
                    :class="tableMode[0]"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageSplit[0].currentPage"
                    :page-sizes="pageSplit[0].pageSizeList"
                    :page-size="pageSplit[0].pageSizeNow"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageSplit[0].total">
                </el-pagination>

                <div class="extra-table" :class="tableActiveClass">
                    <table cellpadding="0" cellspacing="0" border="0" class="default-table">
                        <thead>
                            <tr>
                                <th>序号</th>
                                <th>采集时间<i class="el-icon-d-caret" v-on:click="tableSort('time1')"></i></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,ind) in smallTable">
                                <td>{{ind+1}}</td>
                                <td>{{item.CAPTURETIME | dateFormat}}</td>
                            </tr>
                        </tbody>
                    </table>
                    <i class="table-close el-icon-d-arrow-right" v-on:click="tableClose"></i>
                </div>
            </div>
        </div>
        
        <!-- 树形选择器 -->
        <tree-case 
            :activeClass="treeActiveClass"
            @closeTree="treeClose"
            @getCheckedCallback="getNodes">
        </tree-case>
    </div>
</template>
<script>
    import AJAX, { loading, CancelToken, transformRequest } from 'ajax'

    import tab from "./components/tab.vue"
    import tree from "./components/tree.vue"
    import extraList from "./components/datelist.vue"

    export default {
        name: 'searchImei',
        data() {
            return {
                index: '',
                imei: '',
                extraQuery: [],
                options: [{
                    value: '1',
                    label: 'IMSI'
                }, {
                    value: '2',
                    label: 'IMEI'
                }],
                value: '1',
                //  查询开关
                extraSwitchText: "展开",
                extraSwitchIcon: "el-icon-arrow-down",
                extraSwitchClass: '',
                
                //  树参数
                treeActiveClass: 'hidden',
                selectTreeIndex: 0,

                //  列表参数
                tableActiveClass: '',
                tableMode: ["active","hidden"],
                extraTableList: ['hidden'],
                tableData: [],
                tableData1: [],
                smallTable: [],
                selectList: [false,false],

                //  操作按钮-同行关联
                coporationBtn: [
                    {
                        name: "轨迹地图可视化"
                    },{
                        name: "IMSI、IMEI同行",
                    },{
                        name: "关联MAC",
                    },{
                        name: "关联车牌",
                    }
                ],
                pageSplit: [
                    {
                        total: 0,
                        pageSizeNow: 10,
                        pageSizeList: [10,20,30,40],
                        currentPage: 1
                    }
                ],
                ajaxConf: {
                    url: "",
                    url1: "/search/imsi/time-mode",
                    url2: "/search/imei/time-mode",
                    url3: "/search/imsi/location-mode",
                    url4: "/search/imei/location-mode",
                    url5: "/search/imsi/location-mode/address",
                    url6: "/search/imei/location-mode/address",
                    data: {
                        daySort: 'desc',
                        numberSort: 'desc',
                        sort: 'desc',
                        sort1: 'desc',
                        list: [],
                        pageNumber: 1,
                        pageSize: 10
                    }
                }
            }
        },
        components: {
            'tab-case': tab,
            'tree-case': tree,
            'extra-list': extraList
        },
        filters: {
            dateFormat: function(e){
                var time;
                if(parseInt(e)%1000 != 0){
                    e = parseInt(e)*1000;
                }
                time = new Date(parseInt(e));
                if(time == "Invalid Date"){
                    return ;
                }else{
                    return time.toLocaleString().replace(/(.*?\s)(.*?)(\d:.*)/,"$1$3").replace(/\//g,"-");
                }
            }
        },
        methods: {
            //  查询条件初始化
            queryInit: function(){
                var i = 0,datetime,datetime1,_this = this,j,list = [];
                if(this.tableMode[0] == 'active'){
                    if(this.value == 1){
                        this.ajaxConf.url = this.ajaxConf.url1;
                        this.ajaxConf.data.imsi = this.imei;

                    }else if(this.value == 2){
                        this.ajaxConf.url = this.ajaxConf.url2;
                        this.ajaxConf.data.imei = this.imei;
                    }
                }else{
                    if(this.value == 1){
                        this.ajaxConf.url = this.ajaxConf.url3;
                        this.ajaxConf.data.imsi = this.imei;
                    }else if(this.value == 2){
                        this.ajaxConf.url = this.ajaxConf.url4;
                        this.ajaxConf.data.imei = this.imei;
                    }
                }
                
                for(i = 0;i<this.extraQuery.length;i++){
                    if(!this.extraQuery[i].deviceId){
                        this.$message("目标不能为空！");
                        return false;
                    }else{
                        datetime = Date.parse(new Date(_this.extraQuery[i].startTime))/1000;
                        datetime1 = Date.parse(new Date(_this.extraQuery[i].endTime))/1000;
                        if(!isNaN(datetime)&&!isNaN(datetime1)){
                            for(j = 0;j<this.extraQuery[i].deviceId.length;j++){
                                list.push({
                                    startTime: datetime,
                                    endTime: datetime1,
                                    deviceId: this.extraQuery[i].deviceId[j]
                                })
                            }
                        }else{
                            _this.$message("非法的时间格式！在查询条件的第"+(i+1)+"行···");
                            return false;
                        }
                    }
                }
                this.ajaxConf.data.list = JSON.stringify(list);
                this.dataInit();                
            },
            dataInit: function(page_now,page_size,refresh){
                var _this = this;
                this.ajaxConf.data.pageNumber = page_now || 1;
                this.ajaxConf.data.pageSize = page_size || this.ajaxConf.data.pageSize;

                AJAX.post(_this.ajaxConf.url,_this.ajaxConf.data)
                .then(function(e){
                    if(e.errCode == 0){
                        if(e.list.length == 0){
                            _this.$message("您未查到任何数据！");
                        }else if(_this.tableMode[0] == 'active'){
                            _this.tableData = e.list;
                            _this.tableData.length = e.list.length;
                            if(!refresh){
                                _this.$set(_this.pageSplit,0,{
                                    total: e.total,
                                    pageSizeNow: page_now 
                                });
                            }
                        }else if(_this.tableMode[1] == 'active'){
                            _this.tableData1 = _this.placeDataFormat(e.list);
                            _this.tableData1.length = _this.placeDataFormat(e.list).length;
                        }
                    }else{
                        _this.$message(e.errMsg);
                    }
                })
                .catch(function(e){
                    _this.$message(e.message);
                })
            },
            // 分页控制
            handleSizeChange(val) {
                this.ajaxConf.data.pageSize = val;
                this.dataInit(1,val,1);
            },
            handleCurrentChange(val) {
                this.dataInit(val,0,1);
            },
            //  排序
            tableSort: function(e){
                if(e == 'time'){
                    this.ajaxConf.data.sort = this.ajaxConf.data.sort == 'desc'?"asc":"desc";
                    this.dataInit();
                }else if(e == 'day'){
                    this.ajaxConf.data.daySort = this.ajaxConf.data.daySort == 'desc'?"asc":"desc";
                    this.ajaxConf.data.numberSort = '';
                    this.dataInit();
                }else if(e == 'number'){
                    this.ajaxConf.data.numberSort = this.ajaxConf.data.numberSort == 'desc'?"asc":"desc";
                    this.ajaxConf.data.daySort = '';
                    this.dataInit();
                }else if(e == 'time1'){
                    this.ajaxConf.data.sort1 = this.ajaxConf.data.sort1 == 'desc'?"asc":"desc";
                    this.showTable();
                }
            },
            highLight: function(e){
                this.index = e;
            },
            //  查询条件展开收起
            switchQuery: function(){
                if(this.extraSwitchText == "展开"){
                    this.extraSwitchText = "收起";
                    this.extraSwitchIcon = "el-icon-arrow-up";
                    this.extraSwitchClass = 'active';
                }else{
                    this.extraSwitchText = "展开";
                    this.extraSwitchIcon = "el-icon-arrow-down";
                    this.extraSwitchClass = '';
                }
            },

            //  列表操作
            showTable: function(e,ind){
                var url,_this = this,address = e || sessionStorage.getItem("searchAddress");
                if(this.value == 1){
                    url = this.ajaxConf.url5;
                }else{
                    url = this.ajaxConf.url6;
                }
                AJAX.post(url,{
                    imsi: this.ajaxConf.data.imsi,
                    imei: this.ajaxConf.data.imei,
                    sort: this.ajaxConf.data.sort1,
                    address: address,
                    list: this.ajaxConf.data.list
                }).then(function(e){
                    sessionStorage.setItem("searchAddress",address);
                    if(e.errCode == 0){
                        _this.tableActiveClass = 'active';
                        _this.smallTable = {};
                        _this.smallTable = Object.assign({},_this.smallTable,e.list);
                    }else{
                        _this.$message(e.errMsg);
                    }
                }).catch(function(e){
                    _this.$message(e.message);
                })
                ind?this.highLight(ind):true;
            },
            tableClose: function(){
                this.tableActiveClass = '';
            },
            selectAll: function(e){
                var i = 0,now;
                if(!this.selectList[e]){
                    now = false;
                }else{
                    now = true;
                }
                if(e == 0){
                    for(i = 0;i < this.tableData.length;i++){
                        this.tableData[i].check = now;
                    }  
                }else{
                    for(i = 0;i < this.tableData1.length;i++){
                        this.tableData1[i].check = now;
                    } 
                }      
            },
            //  时间模式地点模式切换
            changeTimeMode: function(){
                this.tableClose();
                this.$set(this.tableMode,0,'active');
                this.$set(this.tableMode,1,'hidden');
            },
            changePlaceMode: function(){
                this.$set(this.tableMode,0,'hidden');
                this.$set(this.tableMode,1,'active');
            },


            // 获取树形菜单被选取的数据
            getNodes(e) {
                this.extraQuery[this.selectTreeIndex].place = e.nodes;
                this.extraQuery[this.selectTreeIndex].deviceId = e.keys;
                this.treeClose();
            },
            showTree: function(e){
                this.selectTreeIndex = e;
                this.treeActiveClass = '';
            },
            treeClose: function(){
                this.treeActiveClass = 'hidden';
            },

            //  列表按钮事件控制--关联同行
            timeThrow(e) {
                var _this = this,i,mark = 0,jsondata = [];

                // 判断该次点击是否有效
                if(this.tableMode[0] == "active"){
                    for(i = 0; i < this.tableData.length;i++){
                        if(!!this.tableData[i].check){
                            jsondata.push({
                                CAPTURETIME: this.tableData[i].CAPTURETIME,
                                DEVICEID: this.tableData[i].DEVICEID,
                                IMEI: this.tableData[i].IMEI,
                                IMSI: this.tableData[i].IMSI,
                                PHONE: this.tableData[i].PHONE,
                                ADDRESS: this.tableData[i].ADDRESS,
                                YYS: this.tableData[i].YYS,
                                TIMEZON: ''
                            });
                            mark = 1;
                        }
                    }
                }else if(this.tableMode[1] == "active"){
                    for(i = 0; i < this.tableData1.length;i++){
                        if(!!this.tableData1[i].check){
                            for(let j = 0;j<this.tableData1[i].row;j++){
                                jsondata.push({
                                    DEVICEID: this.tableData1[j].DEVICEID,
                                    IMEI: this.tableData1[j].imei,
                                    IMSI: this.tableData1[j].imsi,
                                    PHONE: this.tableData1[j].phone,
                                    ADDRESS: this.tableData1[j].ADDRESS,
                                    TIMEZON: ''
                                });
                            }
                            mark = 1;
                        }
                    }
                }

                if(mark == 0){
                    this.$message("您还未选取需要操作的数据！");
                }else{
                    this.$prompt('前后时间差是(单位：秒)', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        inputPattern: /^\d{1,5}.?/,
                        inputErrorMessage: '时间格式不正确'
                    }).then(({ value }) => {
                        if(e == 0){
                            // _this.$router.push({
                            //     name: 'imeiTogether'
                            // });
                            _this.$message("轨迹暂时还没有！");
                        }else if(e == 1){
                            sessionStorage.setItem("search_imei_together",value+'#'+JSON.stringify(jsondata));
                            _this.$router.push({
                                name: 'imeiTogether'
                            });
                        }else if(e == 2){
                            sessionStorage.setItem("search_connect_mac",value+'#'+JSON.stringify(jsondata));
                            _this.$router.push({
                                name: 'macConnect'
                            });
                        }else if(e == 3){
                            sessionStorage.setItem("search_connect_car",value+'#'+JSON.stringify(jsondata));
                            _this.$router.push({
                                name: 'carConnect'
                            });
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '取消输入'
                        });       
                    }); 
                }
            },

            //  按地点模式数据格式化
            placeDataFormat: function(list){
                var destArr = [],i,j;
                for(i = 0;i < list.length;i++){
                    for(j = 0;j<list[i].list.length;j++){
                        if(j == 0){
                            destArr.push({
                                row: list[i].list.length
                            })
                        }else{
                            destArr.push({
                                row: 0
                            })
                        }

                        for(var item1 in list[i]){
                            if(item1 != 'list'){
                                destArr[destArr.length-1][item1] = list[i][item1];
                            }
                        }
                        for(var item in list[i].list[j]){
                            destArr[destArr.length-1][item] = list[i].list[j][item];
                        }
                    }
                }
                return destArr;
            }
        },
        mounted: function(){
            if(!!this.$route.params.key && !!this.$route.params.key.trim()){
                this.imei = this.$route.params.key;
            }
            this.tableData1 = this.placeDataFormat(this.tableData1);
        }
    }
</script>

<style lang="scss" scoped>
    
</style>



