<template>
	<div class="search-wrapper">
		<div class="search-query">
			<div class="basic-query">
				<span>{{pageName}}</span>
				<el-input type="text" placeholder="请输入内容" v-model="mac"></el-input>
				<el-button type="primary" v-on:click="queryMAC">查询</el-button>
				<el-button>清空</el-button>
				<span v-on:click="switchQuery">{{extraSwitchText}} <i :class="extraSwitchIcon"></i></span>
			</div>

			<extra-list
				:activeClass="extraSwitchClass"
				:listData="extraQuery"
				@chooseEvents="showTree">
			</extra-list>
		</div>

		<div class="search-content">
		
			<!-- 选项卡 -->
			<tab-case 
				@handleTab1="changeTimeMode" 
				@handleTab2="changePlaceMode">
			</tab-case>	

			<div class="search-table">
				<div class="search-coperation">
					<el-button>轨迹地图可视化</el-button>
					<el-button v-for="(item,ind) in coporationBtn" v-on:click="timeThrow(ind)" :key="ind">{{item.name}}</el-button>
				</div>
				<table class="default-table time-mode-table" :class="tableMode[0]" cellpadding="0" cellspacing="0" border="0">
					<thead>
						<tr>
							<th><input type="checkbox" /></th>
							<th>{{pageName}}</th>
							<th>标题<i class="el-icon-d-caret"></i></th>
							<th>标题</th>
							<th>标题</th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="item in tableData">
							<td><input type="checkbox"></td>
							<td>{{item.mac}}</td>
							<td>{{item.place}}</td>
							<td>{{item.place1}}</td>
							<td>{{item.place2}}</td>
						</tr>
					</tbody>
				</table>

				<table class="default-table place-mode-table" :class="tableMode[1]" cellpadding="0" cellspacing="0" border="0">
					<thead>
						<tr>
							<th><input type="checkbox" /></th>
							<th>{{pageName}}</th>
							<th>标题1<i class="el-icon-d-caret"></i></th>
							<th>标题1</th>
							<th>标题1</th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="item in tableData">
							<td><input type="checkbox"></td>
							<td>{{item.mac}}</td>
							<td v-on:click="showTable">{{item.place}}</td>
							<td>{{item.place1}}</td>
							<td>{{item.place2}}</td>
						</tr>
					</tbody>
				</table>
				<div class="search-coperation">
					<el-button>轨迹地图可视化</el-button>
					<el-button v-for="(item,ind) in coporationBtn" v-on:click="timeThrow(ind)" :key="ind">{{item.name}}</el-button>
				</div>
				<el-pagination
					:class="tableMode[0]"
			        @size-change="handleSizeChange"
	  		        @current-change="handleCurrentChange"
	    		    :current-page="pageSplit[0].currentPage"
	    		    :page-sizes="pageSplit[0].pageSizeList"
	    		    :page-size="pageSplit[0].pageSizeNow"
	    		    layout="total, sizes, prev, pager, next, jumper"
	    		    :total="pageSplit[0].total">
			    </el-pagination>

			    <div class="extra-table" :class="tableActiveClass">
			    	<table cellpadding="0" cellspacing="0" border="0" class="default-table">
				    	<thead>
				    		<tr>
				    			<th>123</th>
				    			<th>54156</th>
				    			<th>45</th>
				    			<th>51</th>
				    		</tr>
				    	</thead>
			    		<tbody>
			    			<tr>
			    				<td>156</td>
			    				<td>45</td>
			    				<td>4515</td>
			    				<td>45</td>
			    			</tr>
			    		</tbody>
			    	</table>
			    	<i class="table-close el-icon-d-arrow-right" v-on:click="tableClose"></i>
			    </div>
			</div>
		</div>
		
		<!-- 树形选择器 -->
		<tree-case 
			:activeClass="treeActiveClass"
			@closeTree="treeClose"
			@getCheckedCallback="getNodes">
		</tree-case>
	</div>
</template>
<script>
	import AJAX, { loading, CancelToken, transformRequest } from 'ajax'

	import tab from "./components/tab.vue"
	import tree from "./components/tree.vue"
	import extraList from "./components/datelist.vue"

 	export default {
 		name: 'searchMac',
	    data() {
	      	return {
	      		pageName: "MAC",
		        mac: '',

		        //	列表参数
		        tableActiveClass: '',
		        tableMode: ["","hidden"],
		        extraTableList: ['hidden'],

		        //	树参数
		        treeActiveClass: 'hidden',
		        selectTreeIndex: 0,

		        //	查询开关
		        extraSwitchText: "展开",
		        extraSwitchIcon: "el-icon-arrow-down",
		        extraSwitchClass: '',

		        //	操作按钮-同行关联
		        coporationBtn: [
		        	{
		        		name: "MAC同行",
		        		link: "/search/macTogether"
		        	},{
		        		name: "关联IMSI",
		        		link: "/search/imsiConnect"
		        	},{
		        		name: "关联IMEI",
		        		link: "/search/imeiConnect"
		        	},{
		        		name: "关联车牌",
		        		link: "/search/carConnect"
		        	}
		        ],

	        	extraQuery: [
	        		{
	        			stime: '',
	        			etime: '',
	        			place: '',
	        			keys: '',
	        		}
	        	],
	        	tableData: [
	        		{
	        			mac: 5161,
	        			place: '5161',
	        			place1: '5161',
	        			place2: '5161'
	        		},{
	        			mac: 5161,
	        			place: '5161',
	        			place1: '5161',
	        			place2: '5161'
	        		}
	        	],
	        	pageSplit: [
	        		{
	        			total: 680,
	        			pageSizeNow: 10,
	        			pageSizeList: [10,20,30,40],
	        			currentPage: 1
	        		}
	        	]
      		}
	    },
	    components: {
	    	'tab-case': tab,
	    	'tree-case': tree,
	    	'extra-list': extraList
	    },
	    watch: {

	    },
	    methods: {
	 		queryMAC: function(){
	 			console.log("查询",this.extraQuery);
	 		},
	    	showTable: function(){
	    		this.tableActiveClass = 'active';
	    	},
	    	tableClose: function(){
	    		this.tableActiveClass = '';
	    	},

	    	//	时间模式地点模式切换
	    	changeTimeMode: function(){
	    		this.$set(this.tableMode,0,'active');
	    		this.$set(this.tableMode,1,'hidden');
	    	},
			changePlaceMode: function(){
				this.$set(this.tableMode,0,'hidden');
	    		this.$set(this.tableMode,1,'active');
			},

	    	// 分页控制
    	 	handleSizeChange(val) {
		        console.log(`每页 ${val} 条`);
	      	},
	      	handleCurrentChange(val) {
		        console.log(`当前页: ${val}`);
	      	},

	      	//	查询条件展开收起
	      	switchQuery: function(){
	      		if(this.extraSwitchText == "展开"){
	      			this.extraSwitchText = "收起";
	      			this.extraSwitchIcon = "el-icon-arrow-up";
	      			this.extraSwitchClass = 'active';
	      		}else{
					this.extraSwitchText = "展开";
	      			this.extraSwitchIcon = "el-icon-arrow-down";
	      			this.extraSwitchClass = '';
	      		}
	      	},

	      	// 获取树形菜单被选取的数据
	      	getNodes(e) {
		        this.extraQuery[this.selectTreeIndex].place = e.nodes;
	         	this.extraQuery[this.selectTreeIndex].keys = e.keys;
	         	this.treeClose();
	      	},
	      	showTree: function(e){
	      		this.selectTreeIndex = e;
	      		this.treeActiveClass = '';
	      	},
	      	treeClose: function(){
	      		this.treeActiveClass = 'hidden';
	      	},

	      	timeThrow(e) {
	      		var _this = this;
		        this.$prompt('前后时间差是(单位：秒)', '提示', {
			        confirmButtonText: '确定',
			        cancelButtonText: '取消',
			        inputPattern: /^\d{1,5}.?/,
			        inputErrorMessage: '时间格式不正确'
		        }).then(({ value }) => {
			        this.$message({
			            type: 'success',
			            message: '你的邮箱是: ' + value
			        });
			        if(e == 0){
			        	_this.$router.push({
							name: 'macTogether',
							params: {
								time: value,
								list: "object" 
							}
						});
			        }
		        }).catch(() => {
		          	this.$message({
			            type: 'info',
			            message: '取消输入'
		          	});       
		        });
	      	}
	    },
	    mounted: function(){
	    	// AJAX.post("123",{"name": "123"}).then(function(){
	    	// 	console.log("asd")
	    	// }).catch(function(){
	    	// 	console.log("error")
	    	// })
	    	this.mac = this.$route.params.key;
	    }
  	}
</script>
<style lang="scss">
    
</style>


