<template>
  <div class="sider">
    <header>
      <el-input type="text" :value="value" @input="onInput" clearable size="small" @keyup.native.enter="onSearch" />
      <el-button class="search" type="primary" size="small" @click="onSearch">搜索</el-button>
    </header>
    <footer>
      <slot />
    </footer>
  </div>
</template>

<script>
  export default {
    props: {
      value: {
        type: String,
        default: ''
      }
    },
    methods: {
      onInput (value) {
        this.$emit('input', value)
      },
      onSearch () {
        this.$emit('onSearch')
      }
    }
  }
</script>

<style lang="scss" scoped>
  .sider {
    display: flex;
    flex-direction: column;

    header {
      flex-shrink: 0;
      display: flex;
      padding: 15px 20px;
      background-color: #fff;
      border: 1px solid rgba(228, 228, 228, 1);
      box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.34902);

      .search {
        margin-left: 20px;
      }
    }

    footer {
      flex-grow: 1;
      padding: 30px 10px;
      margin-top: 10px;
      background-color: #fff;
      border: 1px solid rgba(228, 228, 228, 1);
      box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.34902);
      color: #444;
      font-size: 14px;
      overflow: auto;
    }
  }
</style>



