import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/source/layout/layout'

import search from '@/source/home/index'
import searchMac from '@/source/dataQuery/mac'
import searchImei from '@/source/dataQuery/imei'
import macConnect from '@/source/dataQuery/macConnect'
import carConnect from '@/source/dataQuery/carConnect'
import imeiTogether from '@/source/dataQuery/imeiTogether'

import togetherMac from '@/source/determine/together/mac'

import IMSITogetherMap from '@/source/IMSITogetherMap'
import IMSIJoinMACMap from '@/source/IMSIJoinMACMap'
import IMSIJoinCarMap from '@/source/IMSIJoinCarMap'

Vue.use(Router)

export default new Router({
	routes: [
      	{
		    path: '/',
		    name: 'layout',
    		component: layout,
            children: [              //   内层的子页面在children下面注册
                {
                    path: '/search',
                    name: 'search',
                    component: search,
                },{
                    path: '/search/mac/:key',
                    name: 'searchMac',
                    component: searchMac
                },{
                    path: '/search/imei/:key',
                    name: 'searchImei',
                    component: searchImei
                },{
                    path: '/search/imeiTogether',
                    name: 'imeiTogether',
                    component: imeiTogether
                },{
                    path: '/search/macConnect',
                    name: 'macConnect',
                    component: macConnect
                },{
                    path: '/search/carConnect',
                    name: 'carConnect',
                    component: carConnect
                },{
                    path: '/together/mac',
                    name: 'together',
                    component: togetherMac,
                    beforeEnter: function(to,from,next){
                        next();
                    }
                },
                //  地图模块
                {
                    path: '/map/IMSITogetherMap',
                    name: 'IMSITogetherMap',
                    component: IMSITogetherMap
                },{
                    path: '/map/IMSIJoinMACMap',
                    name: 'IMSIJoinMACMap',
                    component: IMSIJoinMACMap
                },{
                    path: '/map/IMSIJoinCarMap',
                    name: 'IMSIJoinCarMap',
                    component: IMSIJoinCarMap
                }
           ]
      	}
  	]
})




