<template>
  <div v-show="false">
    <slot />
  </div>
</template>

<script>
  import sleep from 'sleep'
  import pointImage from '../../assets/img/point.png'
  import pointHighlightImage from '../../assets/img/point-highlight.png'

  const pointIcon = new TYMap.Icon(pointImage, new TYMap.Size(27, 32), {
    offset: new TYMap.Size(14, 32)
  })
  const pointHighlightIcon = new TYMap.Icon(pointHighlightImage, new TYMap.Size(35, 50), {
    offset: new TYMap.Size(18, 50)
  })

  export default {
    props: {
      lng: {
        type: Number,
        required: true
      },
      lat: {
        type: Number,
        required: true
      },
      highlight: {
        type: Boolean,
        default: false
      }
    },
    inject: ['getMap'],
    provide () {
      return {
        getMarker: this.getMarker
      }
    },
    data () {
      let resolve
      let inited = new Promise(r => resolve = r)
      return {
        inited,
        resolve
      }
    },
    watch: {
      lng (nv, ov) {
        if (nv !== ov) {
          this.setPosition(nv, this.lat)
        }
      },
      lat (nv, ov) {
        if (nv !== ov) {
          this.setPosition(this.lng, nv)
        }
      },
      highlight (nv, ov) {
        if (nv !== ov) {
          if (nv) {
            this.marker.setTop(true)
            this.marker.setIcon(pointHighlightIcon)
          } else {
            this.marker.setTop(false)
            this.marker.setIcon(pointIcon)
          }
        }
      }
    },
    methods: {
      getMarker () {
        return this.inited
      },
      setPosition (lng, lat) {
        this.marker.setPosition(new TYMap.Point(lng, lat))
      }
    },
    async created () {
      let map = await this.getMap()
      let marker = new TYMap.Marker(new TYMap.Point(this.lng, this.lat), {
        icon: pointIcon
      })
      marker.addEventListener('click', () => {
        if (!this.highlight) {
          map.closeInfoWindow()
        }
        this.$emit('onClick')
      })
      this.marker = marker
      map.addOverlay(this.marker)
      this.resolve(this.marker)
    },
    async destroyed () {
      let map = await this.getMap()
      map.removeOverlay(this.marker)
    }
  }
</script>



