<template>
	<div class="search-wrapper">
		<div class="search-table search-query">
			<el-table :data="jsondata" height="250" style="width: 100%;" highlight-current-row>
			    <el-table-column prop="IMSI" label="IMSI" ></el-table-column>
			   	<el-table-column prop="IMEI" label="IMEI" ></el-table-column>
			   	<el-table-column prop="PHONE" label="手机号码" ></el-table-column>
			   	<el-table-column prop="YYS" label="运营商" ></el-table-column>
			   	<el-table-column prop="CAPTURETIME" :label="'时间('+jsondata.length+')'"></el-table-column>
			   	<el-table-column prop="ADDRESS" label="地点" ></el-table-column>
		  	</el-table>
		</div>

		<div class="search-content">

			<div class="search-table">
				<div class="search-coperation">
					<el-button>轨迹地图可视化</el-button>
				</div>
				<table class="default-table place-mode-table" cellpadding="0" cellspacing="0" border="0">
					<thead>
						<tr>
							<th><input type="checkbox" /></th>
							<th>{{pageName}}</th>
							<th>区域数</th>
							<th>地点数<i class="el-icon-d-caret" v-on:click="sortControl(0)"></i></th>
							<th>时间数<i class="el-icon-d-caret" v-on:click="sortControl(1)"></i></th>
							<th>地点匹配度</th>
							<th>时间匹配度</th>
							<th>最早时间</th>
							<th>最晚时间</th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="(item,ind) in tableData.list" :keys="item.id">
							<td><input type="checkbox"></td>
							<td v-on:click="showTable(0,ind)" class="pointer">{{item.data}}</td>
							<td v-on:click="showTable(1,ind)" class="pointer">{{item.areas}}</td>
							<td v-on:click="showTable(2,ind)" class="pointer">{{item.locations}}</td>
							<td>{{item.times}}</td>
							<td>{{item.localtionMatch}}</td>
							<td v-on:click="showTable(3,ind)" class="pointer">{{item.timeMatch}}</td>
							<td>{{item.earlyTime | dateFormat}}</td>
							<td>{{item.lateTime | dateFormat}}</td>
						</tr>
					</tbody>
				</table>

				<!-- <el-table :data="tableData.list" height="550" style="width: 100%;" highlight-current-row @selection-change="handleSelectionChange">
					<el-table-column type="selection"></el-table-column>
				    <el-table-column prop="data" label="车牌" ></el-table-column>
				   	<el-table-column prop="areas" label="区域数" ></el-table-column>
				   	<el-table-column prop="locations" label="地点数" ></el-table-column>
				   	<el-table-column prop="times" label="时间数" ></el-table-column>
				   	<el-table-column prop="localtionMatch" label="地点匹配度" ></el-table-column>
				   	<el-table-column prop="timeMatch" label="时间匹配度"></el-table-column>
				   	<el-table-column prop="earlyTime" label="最早时间" ></el-table-column>
				   	<el-table-column prop="lateTime" label="最晚时间" ></el-table-column>
			  	</el-table> -->
				<div class="search-coperation bottom-coperation">
					<el-button>轨迹地图可视化</el-button>
				</div>
				<el-pagination
			        @size-change="handleSizeChange"
	  		        @current-change="handleCurrentChange"
	    		    :current-page="pageSplit[0].currentPage"
	    		    :page-sizes="pageSplit[0].pageSizeList"
	    		    :page-size="pageSplit[0].pageSizeNow"
	    		    layout="total, sizes, prev, pager, next, jumper"
	    		    :total="pageSplit[0].total">
			    </el-pagination>

			    <div class="extra-table" :class="tableActiveClass">

			    	<!-- imsi、imei -->
			    	<table cellpadding="0" cellspacing="0" border="0" class="default-table" :class="extraTableList[0]">
				    	<thead><tr><th>序号</th><th>采集时间<i class="el-icon-d-caret" v-on:click="sortControl(2)"></i></th><th>采集地点</th></tr></thead>
			    		<tbody>
			    			<tr v-for="(item,ind) in tableData.default">
			    				<td>{{ind+1}}</td>
			    				<td>{{item.time | dateFormat}}</td>
			    				<td>{{item.location}}</td>
			    			</tr>
			    		</tbody>
			    	</table>

					<!-- 按区域数继续查询 -->
					<table cellpadding="0" cellspacing="0" border="0" class="default-table" :class="extraTableList[1]">
				    	<thead><tr><th>序号</th><th>区域名称</th><th>采集次数</th></tr></thead>
			    		<tbody>
			    			<tr v-for="(item,ind) in tableData.byArea">
			    				<td>{{ind+1}}</td>
			    				<td>{{item.area}}</td>
			    				<td>{{item.areaNumber}}</td>
			    			</tr>
			    		</tbody>
			    	</table>

			    	<!-- 按地点数查询 -->
			    	<table cellpadding="0" cellspacing="0" border="0" class="default-table" :class="extraTableList[2]">
				    	<thead>
				    		<tr>
				    			<th>序号</th>
			    				<th>采集地点</th>
			    				<th>采集次数<i class="el-icon-d-caret" v-on:click="sortControl(3)"></i></th>
			    				<th>最早时间<i class="el-icon-d-caret" v-on:click="sortControl(4)"></i></th>
			    				<th>最晚时间<i class="el-icon-d-caret" v-on:click="sortControl(5)"></i></th>
		    				</tr>
	    				</thead>
			    		<tbody>
			    			<tr v-for="(item,ind) in tableData.byPlace">
			    				<td>{{ind+1}}</td>
			    				<td>{{item.address}}</td>
			    				<td>{{item.collectNumber}}</td>
			    				<td>{{item.earlyTime | dateFormat}}</td>
			    				<td>{{item.lateTime | dateFormat}}</td>
			    			</tr>
			    		</tbody>
			    	</table>

					<!-- 按时间匹配度查询 -->
					<table cellpadding="0" cellspacing="0" border="0" class="default-table" :class="extraTableList[3]">
				    	<thead><tr><th>序号</th><th>采集地点</th><th>最早时间</th><th>最晚时间</th></tr></thead>
			    		<tbody>
			    			<tr v-for="(item,ind) in tableData.byTime">
			    				<td v-if="item.row>0" :rowspan="item.row">{{ind+1}}</td>
			    				<td v-if="item.row>0" :rowspan="item.row">{{item.address}}</td>
			    				<td>{{item.collecTimeLeft | dateFormat}}</td>
			    				<td>{{item.collecTimeRight | dateFormat}}</td>
			    			</tr>
			    		</tbody>
			    	</table>

			    	<i class="table-close el-icon-d-arrow-right" v-on:click="tableClose"></i>
			    </div>
			</div>
		</div>
	</div>
</template>
<script>
	import AJAX, { loading, CancelToken, transformRequest } from 'ajax'

 	export default {
 		name: 'carConnect',
	    data() {
	      	return {
	      		pageName: "车牌",
	      		jsondata: [],
	      		page_size: 10,

		        //	列表参数
		        tableActiveClass: '',
		        extraTableList: ['hidden','hidden','hidden','hidden'],
		        multipleSelection: [],
		        smallTableAjax: [
		        	{
		        		url: "/contact/carplate/carplate",
		        		jsondata: {
		        			carplate: "",
		        			timeSort: 'desc',
		        			list: ""
		        		}
		        	},{
		        		url: "/contact/carplate/areas",
		        		jsondata: {
		        			carplate: "",
		        			colSort: 'desc',
		        			list: ""
		        		}
		        	},{
		        		url: "/contact/carplate/address",
		        		jsondata: {
		        			carplate: "",
		        			colSort: 'desc',
		        			earlyTimeSort: '',
		        			lateTimeSort: '',
		        			list: ""
		        		}
		        	},{
		        		url: "/contact/carplate/times",
		        		jsondata: {
		        			carplate: "",
		        			list: ""
		        		}
		        	},{
		        		jsondata: {
		        			addSort: 'desc',
							timeSort: 'desc'
		        		}
		        	}
		        ],

		        //	查询开关
		        extraSwitchText: "展开",
		        extraSwitchIcon: "el-icon-arrow-down",
		        extraSwitchClass: '',

	        	tableData: {
	        		list: [],
	        		default: [],
	        		byArea: [],
	        		byPlace: [],
	        		byTime: []
	        	},
	        	pageSplit: [
	        		{
	        			total: 0,
	        			pageSizeNow: 10,
	        			pageSizeList: [10,20,30,40],
	        			currentPage: 1
	        		}
	        	]
      		}
	    },
	    filters: {
            dateFormat: function(e){
                var time;
                if(parseInt(e)%1000 != 0){
                    e = parseInt(e)*1000;
                }
                time = new Date(parseInt(e));
                if(time == "Invalid Date"){
                    return ;
                }else{
                    return time.toLocaleString().replace(/(.*?\s)(.*?)(\d:.*)/,"$1$3").replace(/\//g,"-");
                }
            }
        },
	    methods: {
	 		pageInit: function(){
	 			//	参数初始化打包
	 			var time = sessionStorage.getItem("search_connect_car").split("#")[0],
	 				jsondata = sessionStorage.getItem("search_connect_car").split("#")[1],
	 				i = 0;
 				jsondata = JSON.parse(jsondata);
 				for(i = 0;i<jsondata.length;i++){
 					jsondata[i].TIMEZON = time;
 				}
 				this.jsondata = jsondata;
	 			this.dataInit();
	 		},
	 		dataInit: function(page_now,page_size,refresh){
	 			var _this = this;
	 			AJAX.post("/contact/carplate/main",{
	 				pageNumber: page_now || 1,
	 				pageSize: page_size || _this.page_size,
	 				addSort: _this.smallTableAjax[4].jsondata.addSort,
	 				timeSort: _this.smallTableAjax[4].jsondata.timeSort,
	 				list: JSON.stringify(_this.jsondata)
	 			}).then(function(e){
	 				if(e.errCode == 0){
	 					if(e.list.length == 0){
                            _this.$message("您未查到任何数据！");
                        }else{
                        	_this.tableData.list = e.list;
                        	_this.tableData.list.length = e.list.length;
                        	if(!refresh){
                                _this.$set(_this.pageSplit,0,{
                                    total: e.total,
                                    pageSizeNow: page_now 
                                });
                            }
                        }
	 				}else{
	 					_this.$message(e.errMsg);
	 				}
	 			}).catch(function(e){
	 				_this.$message(e.message || "请求失败，请稍后再试！");
	 			})
	 		},
	    	showTable: function(e,ind){
	    		var _this = this;
	    		this.extraTableList = ["hidden","hidden","hidden","hidden"];
	    		this.$set(this.extraTableList,e,'');
	    		this.tableActiveClass = 'active';

	    		this.smallTableAjax[e].jsondata.list = JSON.stringify(_this.jsondata);
	      		this.smallTableAjax[e].jsondata.carplate = this.tableData.list[ind].data;
	      		this.smallTableInit(e);
	    	},
	    	tableClose: function(){
	    		this.tableActiveClass = '';
	    	},
			smallTableInit: function(e){
	      		var _this = this;
	      		AJAX.post(_this.smallTableAjax[e].url,_this.smallTableAjax[e].jsondata).then(function(d){
	      			if(d.errCode == 0){
	 					if(d.list.length == 0){
                            _this.$message("您未查到任何数据！");
                        }else{
                        	if(e == 0){
                        		_this.tableData.default = d.list;
	      					}else if(e == 1){
	      						_this.tableData.byArea = d.list;
	      					}else if(e == 2){
	      						_this.tableData.byPlace = d.list;
	      					}else if(e == 3){
	      						_this.tableData.byTime = _this.placeDataFormat(d.list);
	      					}
                        }
	 				}else{
	 					_this.$message(e.errMsg);
	 				}
	      		}).catch(function(){
	      			_this.$message(e.message || "请求失败，请稍后再试！");
	      		});
	      	},

	    	// 分页控制
    	 	handleSizeChange(val) {
	 			this.page_size = val;
                this.dataInit(1,val,1);
	      	},
	      	handleCurrentChange(val) {
				this.dataInit(val,0,1);
	      	},
	      	//	排序控制
	      	sortControl: function(e){
	      		if(e == 0){
	      			this.smallTableAjax[4].jsondata.addSort = this.smallTableAjax[4].jsondata.addSort == 'desc'?"asc":"desc";
	      			this.smallTableAjax[4].jsondata.timeSort = '';
	      			this.dataInit();
	      		}else if(e == 1){
	      			this.smallTableAjax[4].jsondata.timeSort = this.smallTableAjax[4].jsondata.timeSort == 'desc'?"asc":"desc";
	      			this.smallTableAjax[4].jsondata.addSort = '';
	      			this.dataInit();
	      		}else if(e == 2){
	      			this.smallTableAjax[0].jsondata.timeSort = this.smallTableAjax[0].jsondata.timeSort == 'desc'?"asc":"desc";
	      			this.smallTableInit(0);
	      		}else if(e == 3){
	      			this.smallTableAjax[1].jsondata.colSort = this.smallTableAjax[1].jsondata.colSort == 'desc'?"asc":"desc";
	      			this.smallTableInit(1);
	      		}else if(e == 4){
	      			this.smallTableAjax[2].colSort = this.smallTableAjax[2].colSort == 'desc'?"asc":"desc";
					this.smallTableAjax[2].earlyTimeSort = '';
					this.smallTableAjax[2].lateTimeSort = '';
					this.smallTableInit(2);
	      		}else if(e == 5){
	      			this.smallTableAjax[2].earlyTimeSort = this.smallTableAjax[2].earlyTimeSort == 'desc'?"asc":"desc";
					this.smallTableAjax[2].colSort = '';
					this.smallTableAjax[2].lateTimeSort = '';
					this.smallTableInit(2);
	      		}else if(e == 6){
	      			this.smallTableAjax[2].lateTimeSort = this.smallTableAjax[2].lateTimeSort == 'desc'?"asc":"desc";
					this.smallTableAjax[2].colSort = '';
					this.smallTableAjax[2].earlyTimeSort = '';
					this.smallTableInit(2);
	      		}
	      	},
	      	
	      	//	行合并数据格式化
	      	placeDataFormat: function(list){
                var destArr = [],i,j;
                for(i = 0;i < list.length;i++){
                    for(j = 0;j<list[i].list.length;j++){
                        if(j == 0){
                            destArr.push({
                                row: list[i].list.length
                            })
                        }else{
                            destArr.push({
                                row: 0
                            })
                        }

                        for(var item1 in list[i]){
                            if(item1 != 'list'){
                                destArr[destArr.length-1][item1] = list[i][item1];
                            }
                        }
                        for(var item in list[i].list[j]){
                            destArr[destArr.length-1][item] = list[i].list[j][item];
                        }
                    }
                }
                return destArr;
            }
	    },
	    filters: {
	    	dateFormat: function(e){
                var time;
                if(parseInt(e)%1000 != 0){
                    e = parseInt(e)*1000;
                }
                time = new Date(parseInt(e));
                if(time == "Invalid Date"){
                    return ;
                }else{
                    return time.toLocaleString().replace(/(.*?\s)(.*?)(\d:.*)/,"$1$3").replace(/\//g,"-");
                }
            }
	    },
	    mounted: function(){
	    	this.pageInit();
	    }
  	}
</script>
<style lang="scss" scoped>
	//	表格模块样式
	.search-table{
		&.search-query{
		    height: auto;
		    margin-bottom: 20px;
		    min-height: 50px;
		    padding-bottom: 20px;
			table thead th{
				background: #efefef;
			}
		}
	}
</style>


