<template>
  <div class="point" :class="{ 'point-highlight': highlight }">
    <div>
      <span class="imsi">
        <span>IMSI：</span>
        <span class="value" v-html="imsi" @click="onClick" />
      </span>
      <span>地点数：{{ point.addressCount }}</span>
      <span>采集数：{{ point.collectCount }}</span>
      <span>区域数：{{ point.areaCount }}</span>
    </div>
    <div>最近采集时间：{{ point.time }}</div>
  </div>
</template>

<script>
  export default {
    props: {
      imsi: {
        type: String,
        required: true
      },
      point: {
        type: Object,
        default: () => ({})
      },
      highlight: {
        type: Boolean,
        default: false
      }
    },
    inject: ['getMap'],
    methods: {
      async onClick (index) {
        if (!this.highlight) {
          let map = await this.getMap()
          map.closeInfoWindow()
        }
        this.$emit('onClick')
      }
    }
  }
</script>

<style lang="scss" scoped>
  .point {
    line-height: 30px;
    white-space: nowrap;

    > div {
      display: flex;
    }

    > div:first-child {
      transition: .3s;

      .imsi {
        color: #1d9ed7;
        font-weight: bold;
        transition: .3s;

        .value {
          cursor: pointer;
        }

        .value:hover {
          text-decoration: underline;
        }
      }

      > span:not(:first-child) {
        margin-left: 10px;
      }
    }
  }

  .point:not(:first-child) {
    margin-top: 20px;
  }

  .point-highlight {
    > div:first-child {
      color: #ff0000;

      .imsi {
        color: #ff0000;
      }
    }
  }
</style>



