<template>
  <ty-map ref="tymap">
    <ty-map-point v-for="({ lng, lat, address, collectCount }, index) of filteredPoints" :key="`${lng},${lat}`" :lng="lng" :lat="lat" :highlight="highlight === index" @onClick="onClickPoint(index)">
      <ty-map-label :text="collectCount > 99 ? '99+' : String(collectCount)" :offset="labelOffset" :css="highlight === index ? labelHighlightStyle : labelStyle" />
    </ty-map-point>
    <ty-map-info-window :content="infoWindowContent" :width="220" :height="90" :offset="infoWindowOffset" :point="highlightPoint" @onClose="onClickPoint()" />
    <ty-map-sider slot="sider" v-model="keyword" @onSearch="onSearch">
      <sider-point v-for="(point, index) of filteredPoints" :key="point.car" :car="em(point.car)" :point="point" :highlight="highlight === index" @onClick="onClickSiderPoint(index)" />
    </ty-map-sider>
  </ty-map>
</template>

<script>
  import sleep from 'sleep'
  import points from './points'
  import Map from '../Map'
  import Point from '../Map/point'
  import Label from '../Map/label'
  import InfoWindow from '../Map/info.window'
  import Sider from '../Map/sider'
  import SiderPoint from './point'

  const labelOffset = new TYMap.Size(3, 6)
  const labelStyle = {
    width: '20px',
    textAlign: 'center',
    height: '14px',
    lineHeight: '14px',
    backgroundColor: 'none',
    border: 'none',
    color: '#fff',
    fontSize: '12px',
    fontWeight: 'bold'
  }
  const labelHighlightStyle = {
    ...labelStyle,
    width: '27px',
    height: '20px',
    lineHeight: '20px',
    fontSize: '14px'
  }
  const infoWindowOffset = new TYMap.Size(0, -55)

  export default {
    data () {
      return {
        keyword: '',
        filteredKeyword: '',
        points,
        highlight: undefined,
        filteredPoints: points,
        labelOffset,
        labelStyle,
        labelHighlightStyle,
        infoWindowOffset
      }
    },
    computed: {
      regexp () {
        return new RegExp(this.filteredKeyword, 'g')
      },
      infoWindowContent () {
        if (this.highlight === undefined) {
          return ''
        }
        let { car, address, collectCount } = this.filteredPoints[this.highlight]
        return `
          <div style="line-height: 30px; font-size: 14px; white-space: nowrap;">
            <div style="font-weight: bold;">车牌号：${this.em(car)}</div>
            <div style="overflow: hidden; text-overflow: ellipsis;" title="${address}">地点：${address}</div>
            <div>采集次数：${collectCount}</div>
          </div>
        `
      },
      highlightPoint () {
        if (this.highlight === undefined) {
          return null
        }
        let { lng, lat } = this.filteredPoints[this.highlight]
        return new TYMap.Point(lng, lat)
      }
    },
    watch: {
      async filteredKeyword (nv, ov) {
        let keyword = nv.trim()
        if (keyword !== ov.trim()) {
          let highlight
          let filteredPoints = keyword ? this.points.filter(({ car }) => car.indexOf(keyword) !== -1) : this.points
          if (this.highlight !== undefined) {
            let car = this.filteredPoints[this.highlight].car
            let index = filteredPoints.findIndex(point => car === point.car)
            highlight = index === -1 ? undefined : index
          }
          this.onClickPoint()
          this.filteredPoints = filteredPoints
          await sleep()
          this.onClickPoint(highlight)
        }
      }
    },
    methods: {
      em (car) {
        return this.filteredKeyword ? car.replace(this.regexp, `<em class="em">${this.filteredKeyword}</em>`) : car
      },
      onClickPoint (index) {
        this.highlight = index
      },
      onClickSiderPoint (index) {
        this.onClickPoint(index)
        if (this.highlightPoint) {
          this.$refs.tymap.map.moveTo(this.highlightPoint)
        }
      },
      onSearch () {
        this.filteredKeyword = this.keyword.trim()
      }
    },
    components: {
      'ty-map': Map,
      'ty-map-point': Point,
      'ty-map-label': Label,
      'ty-map-info-window': InfoWindow,
      'ty-map-sider': Sider,
      'sider-point': SiderPoint
    }
  }
</script>

<style lang="scss" scoped>
  .em {
    color: #ff0000;
    font-style: normal;
  }
</style>



