<template>
	<div class="together-wrapper">
		<div class="together-query">
			<div class="basic-query">
				<span>{{pageName}}</span>
				<el-input type="text" placeholder="请输入内容"></el-input>
				<el-button type="primary">查询</el-button>
				<el-button>清空</el-button>
				<span v-on:click="switchQuery">{{extraSwitchText}} <i :class="extraSwitchIcon"></i></span>
			</div>
			<div class="extra-query" :class="extraSwitchClass">
				<div class="extra-query-item" v-for="(item,ind) in extraQuery">
					<span class="extra-label">选择地点：</span>
					<input type="text" placeholder="请选择地点" v-model="item.place" v-on:click="showTree(ind)" />

					<span class="extra-label">开始时间：</span>
					<el-date-picker
				      	v-model="item.stime"
				      	type="datetime"
				      	placeholder="选择日期时间"
				      	default-time="12:00:00">
				    </el-date-picker>
				    <span class="extra-label">结束时间：</span>
					<el-date-picker
				      	v-model="item.etime"
				      	type="datetime"
				      	placeholder="选择日期时间"
				      	default-time="12:00:00">
				    </el-date-picker>
				    <small class="el-icon-circle-plus" v-on:click="addRow"></small>
				    <small class="el-icon-remove" v-on:click="deleteRow(ind)"></small>
				</div>
			</div>
		</div>
		<div class="together-table">
			<div class="together-coperation">
				<el-button>轨迹地图可视化</el-button>
				<el-button>MAC同行</el-button>
				<el-button>关联IMSI</el-button>
				<el-button>关联IMEI</el-button>
				<el-button>关联车牌</el-button>
			</div>
			<table class="default-table" cellpadding="0" cellspacing="0" border="0">
				<thead>
					<tr>
						<th><input type="checkbox" /></th>
						<th>{{pageName}}</th>
						<th>标题<i class="el-icon-d-caret"></i></th>
						<th>标题</th>
						<th>标题</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="item in tableData">
						<td><input type="checkbox"></td>
						<td>{{item.mac}}</td>
						<td v-on:click="showTable">{{item.place}}</td>
						<td>{{item.place1}}</td>
						<td>{{item.plac2}}</td>
					</tr>
				</tbody>
			</table>
			<div class="together-coperation">
				<el-button>轨迹地图可视化</el-button>
				<el-button>MAC同行</el-button>
				<el-button>关联IMSI</el-button>
				<el-button>关联IMEI</el-button>
				<el-button>关联车牌</el-button>
			</div>
			<el-pagination
		        @size-change="handleSizeChange"
  		        @current-change="handleCurrentChange"
    		    :current-page="pageSplit[0].currentPage"
    		    :page-sizes="pageSplit[0].pageSizeList"
    		    :page-size="pageSplit[0].pageSizeNow"
    		    layout="total, sizes, prev, pager, next, jumper"
    		    :total="pageSplit[0].total">
		    </el-pagination>

		    <div class="extra-table" :class="tableActiveClass">
		    	<table cellpadding="0" cellspacing="0" border="0" class="default-table">
			    	<thead>
			    		<tr>
			    			<th>123</th>
			    			<th>54156</th>
			    			<th>45</th>
			    			<th>51</th>
			    		</tr>
			    	</thead>
		    		<tbody>
		    			<tr>
		    				<td>156</td>
		    				<td>45</td>
		    				<td>4515</td>
		    				<td>45</td>
		    			</tr>
		    		</tbody>
		    	</table>
		    	<i class="table-close el-icon-d-arrow-right" v-on:click="tableClose"></i>
		    </div>
		</div>

		<!-- 树形插件 -->
		<div class="default-tree" :class="treeActiveClass">
			<div class="default-backmood"></div>
			<div class="tree-case">
				<input placeholder="输入关键字进行过滤" v-model="filterText" />
				<i class="el-icon-close" v-on:click="treeClose"></i>
				<el-tree
					class="filter-tree"
					:data="treeDateTest"
					show-checkbox
					default-expand-all
					node-key="id"
					ref="tree"
					:filter-node-method="filterNode"
					highlight-current
					:props="defaultProps">
				</el-tree>
				<div class="buttons">
					<el-button type="primary" @click="getCheckedNodes">确认</el-button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import AJAX, { loading, CancelToken, transformRequest } from 'ajax'

 	export default {
 		name: 'togetherMac',
	    data() {
	      	return {
	      		pageName: "MAC",
		        mac: '',
		        tableActiveClass: '',

		        treeActiveClass: 'hidden',
		        selectTreeIndex: 0,

		        extraSwitchText: "展开",
		        extraSwitchIcon: "el-icon-arrow-down",
		        extraSwitchClass: '',

		        pickerOptions1: {
		          	shortcuts: [{
			            text: '今天',
			            onClick(picker) {
			                picker.$emit('pick', new Date());
			            }
		          	}, {
			            text: '昨天',
			            onClick(picker) {
			                const date = new Date();
			                date.setTime(date.getTime() - 3600 * 1000 * 24);
			                picker.$emit('pick', date);
			            }
		          	}, {
			            text: '一周前',
			            onClick(picker) {
			                const date = new Date();
			                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
			                picker.$emit('pick', date);
			            }
		          	}]
	        	},
	        	extraQuery: [
	        		{
	        			stime: '',
	        			etime: '',
	        			place: '',
	        			keys: '',
	        		}
	        	],
	        	tableData: [
	        		{
	        			mac: 5161,
	        			place: '5161',
	        			place1: '5161',
	        			place2: '5161'
	        		},{
	        			mac: 5161,
	        			place: '5161',
	        			place1: '5161',
	        			place2: '5161'
	        		}
	        	],
	        	pageSplit: [
	        		{
	        			total: 680,
	        			pageSizeNow: 10,
	        			pageSizeList: [10,20,30,40],
	        			currentPage: 1
	        		}
	        	],
	        	filterText: '',
	        	treeDateTest: [{
			          id: 1,
			          label: '一级 1',
			          children: [{
			            id: 4,
			            label: '二级 1-1',
			            children: [{
			              id: 9,
			              label: '三级 1-1-1'
			            }, {
			              id: 10,
			              label: '三级 1-1-2'
			            }]
			          }]
			        }, {
			          id: 2,
			          label: '一级 2',
			          children: [{
			            id: 5,
			            label: '二级 2-1'
			          }, {
			            id: 6,
			            label: '二级 2-2'
			          }]
			        }, {
			          id: 3,
			          label: '一级 3',
			          children: [{
			            id: 7,
			            label: '二级 3-1'
			          }, {
			            id: 8,
			            label: '二级 3-2'
			          }]
		        }],
		        defaultProps: {
		          	children: 'children',
		          	label: 'label'
		        }
      		}
	    },
	    watch: {
	    	//	树形菜单过滤搜索用
	      	filterText(val) {
	        	this.$refs.tree.filter(val);
	      	}
	    },
	    methods: {
	    	addRow: function(){			//	点击增加一条记录
	    		this.extraQuery.push({
	    			stime: '',
        			etime: '',
        			date: ''
	    		});
	    	},
	    	deleteRow: function(e){		//	点击删除一条记录
	    		var max = this.extraQuery.length;
	    		if(max == 1){
	    			this.$message("至少保留一项！")
	    		}else{
	    			for(var i = e;i<max-1;i++){
		    			this.extraQuery[i] = this.extraQuery[i+1];
		    		}
		    		this.extraQuery.pop();
	    		}
	    	},
	    	showTable: function(){
	    		this.tableActiveClass = 'active';
	    	},
	    	tableClose: function(){
	    		this.tableActiveClass = '';
	    	},

	    	// 分页控制
    	 	handleSizeChange(val) {
		        console.log(`每页 ${val} 条`);
	      	},
	      	handleCurrentChange(val) {
		        console.log(`当前页: ${val}`);
	      	},

	      	switchQuery: function(){
	      		if(this.extraSwitchText == "展开"){
	      			this.extraSwitchText = "收起";
	      			this.extraSwitchIcon = "el-icon-arrow-up";
	      			this.extraSwitchClass = 'active';
	      		}else{
					this.extraSwitchText = "展开";
	      			this.extraSwitchIcon = "el-icon-arrow-down";
	      			this.extraSwitchClass = '';
	      		}
	      	},

	      	// 获取树形菜单被选取的数据
	      	getCheckedNodes() {
		        console.log(this.$refs.tree.getCheckedNodes(),this.$refs.tree.getCheckedKeys());
		        this.extraQuery[this.selectTreeIndex].place = this.getNodeFromList(this.$refs.tree.getCheckedNodes());
	         	this.extraQuery[this.selectTreeIndex].keys = this.$refs.tree.getCheckedKeys();
	         	this.treeClose();
	      	},
	      	getNodeFromList: function(e){
	      		var arr = [],i = 0;
	      		for(i = 0;i<e.length;i++){
	      			arr.push(e[i].label);
	      		}
	      		return arr;
	      	},

	      	filterNode(value, data) {
		        if (!value) return true;
		        return data.label.indexOf(value) !== -1;
	      	},
	      	showTree: function(e){
	      		console.log("show tree!");
	      		this.selectTreeIndex = e;
	      		this.treeActiveClass = '';
	      	},
	      	treeClose: function(){
	      		this.treeActiveClass = 'hidden';
	      	}
	    },
	    mounted: function(){
	    	// AJAX.post("123",{"name": "123"}).then(function(){
	    	// 	console.log("asd")
	    	// }).catch(function(){
	    	// 	console.log("error")
	    	// })
	    }
  	}
</script>
<style lang="scss" scoped>
	.together-wrapper{
		width: calc(100% - 10px);
		height: calc(100% - 10px);
		margin: 5px;
		font-size: 13px;
	    overflow-y: auto;
	}

	//	查询模块样式
	.basic-query{
		width: 100%;
		padding: 5px 0 10px;
		span{
			display: inline-block;
			cursor: pointer;
		}
		div{
			width: 240px;
		}
		input{			
			display: inline-block;
		}
		button{
		    padding: 5px 20px;
		}
	}
	.extra-query{
		border-top: 1px solid #d2d2d2;
		display: none;
		&.active{
			display: block;
		}
		.extra-query-item{
			padding: 10px;
			&>input{
				width: 220px;
				border-radius: 3px;
				border: 1px solid #d2d2d2;
				padding: 10px;
			}
			span.extra-label{
				display: inline-block;
				padding: 0 0 0 10px;
			}
			small{
				font-size: 30px;
				color: #aaaaaa;
				display: inline-block;
				position: relative;
				top: 6px;
				&.el-icon-remove:hover{
					color: red;
				}
				&.el-icon-circle-plus:hover{
					color: green;
				}
			}
		}	
	}

	//	表格模块样式
	.together-table{
		height: 600px;
		position: relative;
		overflow: hidden;
		.together-coperation{
			padding: 5px 10px;
			background: #d8e9f6;
			&:nth-child(3){
				position: absolute;
				bottom: 50px;
				width: calc(100% - 20px);
				left: 0;
			}
			button{
				padding: 5px 10px;
			}
		}
		table.default-table{
			color: #666666;
			width: 100%;
			border: none;
			tbody>tr{
				&:hover td{
					background: #ffffff;
				}
			}
			tr{
				th{
					background: #e2e2e2;
					text-align: left;
					padding: 5px 0;
					border-bottom: 1px solid #dddddd;
					i.el-icon-d-caret{
						color: #aaaaaa;
						cursor: pointer;
						&:hover{
							color: #888888;
						}
					}
				}
				td{
					padding: 5px 0;
					border-bottom: 1px solid #dddddd;
					border-left: none;
					border-right: none;
				}
			}
		}
		.el-pagination{
		    text-align: right;
		    position: absolute;
		    bottom: 15px;
		    right: 10px;
		}
		.extra-table{
			position: absolute;
			width: 50%;
			top: 36px;
			right: -100%;
			background: #ffffff;
			transition: right 1s ease;
			border-radius: 4px;
			box-shadow: 0px 5px 10px #d2d2d2;
			height: 500px;
			overflow: auto;
			&.active{
				right: 0;
			}
			.el-icon-d-arrow-right{
				position: absolute;
				left: 10px;
				top: 48%;
				font-size: 30px;
				color: #d2d2d2;
				transition: left .5s ease;
				cursor: pointer;
				&:hover{
					color: green;
					left: 15px;
				}
			}
		}
	}

	//	默认的树形菜单，需要提取到公用样式
	.default-tree{
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		&.hidden{
			display: none;
		}
		input{
			border: none;
			border-bottom: 1px solid #f2f2f2;
			width: 400px;
			padding: 10px;
			outline: none;
			margin: 0 0 0 10px;
		}
		.el-icon-close{
			position: absolute;
			right: 10px;
			top: 10px;
			font-size: 18px;
			color: #888888;
			cursor: pointer;
			&:hover{
				transform: scale(1.2);
				font-weight: bold;
				color: red;
			}
		}
		.default-backmood{
			position: fixed;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: rgba(0,0,0,.5);
			z-index: 5;
		}
		.tree-case{
			position: fixed;
			z-index: 6;
			width: 560px;
			height: 420px;
			border-radius: 5px;
			background: #ffffff;
			box-shadow: 0 0 20px #323232;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			overflow: auto;
			.buttons{
				width: 100%;
				position: absolute;
				bottom: 0;
				left: 0;
				padding: 5px 0 15px;
				text-align: center; 
			}
		}
	}
</style>


