<template>
  <div v-show="false" />
</template>

<script>
  export default {
    props: {
      text: {
        type: [String, HTMLElement],
        required: true
      },
      offset: {
        type: TYMap.Size,
        default: undefined
      },
      css: {
        type: Object,
        default: () => ({})
      }
    },
    inject: ['getMarker'],
    watch: {
      text (nv, ov) {
        if (nv !== ov) {
          this.label.setContent(nv)
        }
      },
      offset (nv, ov) {
        if (nv !== ov) {
          this.label.setOffset(nv)
        }
      },
      css (nv, ov) {
        if (nv !== ov) {
          this.label.setStyle(nv)
        }
      }
    },
    async created () {
      let label = new TYMap.Label(this.text, {
        offset: this.offset
      })
      label.setStyle(this.css)
      this.label = label
      let marker = await this.getMarker()
      marker.setLabel(this.label)
    }
  }
</script>



