import { Message, Loading } from 'element-ui'
import axios, { CancelToken } from 'axios'
import qs from 'qs'
import config from 'config'
import sleep from 'sleep'

const { server: baseURL } = config

let instance = axios.create({
  baseURL,
  withCredentials: true,
  transformRequest: [
    data => qs.stringify(data, { arrayFormat: 'brackets' })
  ]
})

instance.interceptors.request.use(
  config => config,
  e => Promise.reject(e)
)

instance.interceptors.response.use(
  async ({ status, data }) => {
    if (status === 200) {
      let { resultCode, resultMsg = '' } = data
      if (resultCode === '201') {
        return data.data
      } else if (resultCode === undefined) {
        return data
      } else if (resultCode === 401) {
        Message.error(resultMsg)
        await sleep(1000)
        window.location.reload()
        return Promise.reject(resultMsg)
      }
      Message.error(resultMsg)
      return Promise.reject(resultMsg)
    } else {
      Message.error(status)
      return Promise.reject(status)
    }
  },
  async e => {
    if (!e.__CANCEL__) {
      Message.error(e.message)
    }
    return Promise.reject(e)
  }
)

const loading = async function (ajax = this) {
  let loadingInstance = Loading.service()
  try {
    await ajax
  } catch (e) {
  }
  loadingInstance.close()
  return ajax
}

const transformRequest = axios.defaults.transformRequest

export default instance

export {
  loading,
  transformRequest,
  CancelToken
}



