<template>
  	<div class="wrapper">
  		<app-menu></app-menu>
  		<div class="right-content">
  			<app-header></app-header>
  			<div class="content-wrapper">
  				<router-view></router-view>
  			</div>
  			<app-footer></app-footer>
  		</div>
	</div>
</template>

<script>
	import $ from 'jquery'
	import appMenu from './appMenu'
	import appHeader from './appHeader'
	import appFooter from './appFooter'


	export default {
	  	name: 'togetherMac',
	  	data () {
	    	return {
	      		
	    	}
	  	},
	  	methods: {
			handle: function(msg){
				console.log(this.isCollapse);
			}
	  	},
	  	computed: {
	  		isCollapse: function(){
	  			return store.state.menuStatus;
	  		}
	  	},
	  	components: {
	  		'app-menu': appMenu,
	  		'app-header': appHeader,
	  		'app-footer': appFooter
	  	},
	  	mounted: function(){
	  		
	  	}
	}

</script>

<style lang="scss">
	$btn-height: 32px;
    $bordor-color: #d2d2d2;
    $coop-color: #e6f7ff;
    $active-color: #1890ff;

	*{
		padding: 0;
		margin: 0;
	}
	html,body,#app,.wrapper{
		width: 100%;
		height: 100%;
	}
	.right-content{
		width: calc(100% - 240px);
		float: right;
		height: 100%;
		.content-wrapper{
			width: 100%;
			height: calc(100% - 80px);
			background: #efefef;
			float: left;
		}
	}
	.hidden{
        display: none;
    }
    
    .search-wrapper{
        width: calc(100% - 60px);
        height: calc(100% - 50px);
        margin: 15px;
        font-size: 13px;
        overflow-y: auto;
        background: white;
        border-radius: 5px;
        padding: 10px 15px;
    }

    //  查询搜索模块样式
    .basic-query{
        width: 100%;
        padding: 5px 0 10px;
        .el-input--suffix input{
            height: 32px;
        }
        .el-select{
        	width: 100px!important;
        	input{
        		width: 100px!important;
        	}
        }
        &>span{
            display: inline-block;
            width: 90px;
            text-align: right;
            &.search-expand{
                margin: 0 12px;
                color: $active-color;
                cursor: pointer;
            }
        }
        &>input{
            width: 240px;
            border-radius: 2px;   
            height: 32px!important;
            line-height: 32px;
            border: none;
            box-shadow: 0 0 1px #888888;
            padding: 0 0 0 10px;
            margin: 0 10px;
        }
        button{
            height: $btn-height;
            line-height: $btn-height;
            padding: 0 20px;
        }
    }

    //  表格样式
    .search-table{
        min-height: 666px;
        position: relative;
        overflow-y: auto;
        overflow-x: hidden;
        background: white;
        padding-bottom: 92px;
        .search-coperation{
            padding: 10px;
            background: $coop-color;
            text-align: left;
            button{
                height: 32px;
                line-height: 32px;
                padding: 0 15px;
            }
            &.bottom-coperation{
                position: absolute;
                bottom: 50px;
                width: calc(100% - 20px);
                left: 0;
            }
        }
        table.default-table{
            color: #666666;
            width: 100%;
            border: none;
            tbody>tr{
                &:hover td{
                    background: #f2f2f2;
                }
            }
            tr{
                &.active td{
                    background: #f2f2f2;
                }
                th{
                    height: 54px;
                    line-height: 54px;
                    text-align: left;
                    padding-left: 5px;
                    border-bottom: 1px solid #dddddd;
                    color: rgba(0,0,0,0.85);
                    i.el-icon-d-caret{
                        color: #aaaaaa;
                        cursor: pointer;
                        &:hover{
                            color: #888888;
                        }
                    }
                }
                td{
                    padding-left: 5px;
                    height: 54px;
                    line-height: 54px;
                    border-bottom: 1px solid #dddddd;
                    border-left: none;
                    border-right: none;
                    color: rgba(0,0,0,0.65);
                    div{
                        position: relative;
                        left: -5px;
                        width: calc(100% + 5px);
                        height: 100%;
                        background: #ffffff;
                    }
                    &.pointer{
                        cursor: pointer;
                        color: $active-color;
                        &:active{
                            color: #409EFF;
                        } 
                    }
                    &.bl{
                        border-left: 1px solid $bordor-color!important;
                    }
                }
            }
            input[type="checkbox"]{
                -webkit-appearance: none;
                appearance: none;
                background: url("../../assets/images/checkbox_default.png") no-repeat;
                width: 18px;
                height: 18px;
                position: relative;
                top: 3px;
                outline: none;
                &:checked{
                    background: url("../../assets/images/checkbox.png") no-repeat;
                }
            }
        }
        .el-pagination{
            text-align: right;
            position: absolute;
            bottom: 15px;
            right: 10px;
        }
        .extra-table{
            position: fixed;
            text-align: center;
            width: 50%;
            top: 20%;
            right: -100%;
            background: #ffffff;
            transition: right 1s ease;
            border-radius: 4px;
            box-shadow: 4px 4px 24px rgba(24,144,255,0.23);
            height: 500px;
            overflow: auto;
            &.active{
                right: 0;
            }
            .el-icon-d-arrow-right{
                position: absolute;
                left: 10px;
                top: 48%;
                font-size: 30px;
                color: #d2d2d2;
                transition: left .5s ease;
                cursor: pointer;
                &:hover{
                    color: green;
                    left: 15px;
                }
            }
            table thead th{
                background: #1890ff;
                color: #ffffff;
                text-align: center;
                i{
                    color: #ffffff!important;
                }
            }
        }
    }
</style>



