<template>
	<!-- 树形插件 -->
	<div class="default-tree" :class="activeClass">
		<div class="default-backmood"></div>
		<div class="tree-case">
			<input placeholder="输入关键字进行过滤" v-model="filterText" />
			<i class="el-icon-close" v-on:click="treeClose"></i>
			<el-tree
				class="filter-tree"
				:data="treeDateTest"
				show-checkbox
				default-expand-all
				node-key="id"
				ref="tree"
				:filter-node-method="filterNode"
				highlight-current
				:props="defaultProps">
			</el-tree>
			<div class="buttons">
				<el-button type="primary" @click="getCheckedNodes">确认</el-button>
			</div>
		</div>
	</div>
</template>
<script>
	import AJAX, { loading, CancelToken, transformRequest } from 'ajax'

	export default {
		name: 'tree',
		data() {
			return {
				// treeActiveClass: '',
		        selectTreeIndex: 0,

				filterText: '',
	        	treeDateTest: [],
		        defaultProps: {
		          	children: 'children',
		          	label: 'name'
		        }
			}
		},
		methods: {
			//	过滤搜索
			filterNode(value, data) {
		        if (!value) return true;
		        return data.name.indexOf(value) !== -1;
	      	},

	      	// 获取树形菜单被选取的数据
	      	getCheckedNodes() {
	      		var _this = this,
	      			data;
		        if(!this.$refs.tree.getCheckedKeys()){
		        	return false;
		        }else{
		        	data = {
		        		nodes: _this.getNodeFromList(_this.$refs.tree.getCheckedNodes()),
		        		keys: _this.arrTrim(_this.$refs.tree.getCheckedKeys())
		        	}
			        this.$emit("getCheckedCallback",data);
		        }
	      	},
	      	arrTrim: function(arr){
	      		var dest = new Array();
	      		for(let i = 0;i<arr.length;i++){
	      			if(!arr[i]){
	      				continue;
	      			}else{
	      				dest.push(arr[i]);
	      			}
	      		}
	      		return dest;
	      	},
	      	getNodeFromList: function(e){
	      		var arr = [],i = 0;
	      		for(i = 0;i<e.length;i++){
	      			arr.push(e[i].name);
	      		}
	      		return arr;
	      	},
	      	treeClose: function(){
	      		this.$emit("closeTree");
	      	},
	      	treeFormat: function(tree){
			    var i = 0,_this = this;
			    if(!!tree.device && tree.device.length!=0){          //  存在device
			        for(i = 0;i < tree.device.length; i++){
			            tree.child.push(tree.device[i]);
			        }
			        delete tree.device
			    }
			    if(!!tree.child && tree.child.length!=0){
			        tree.children = [];
			        for(i = 0;i < tree.child.length; i++){
			            tree.children[i] = _this.treeFormat(tree.child[i]);
			        }
			        delete tree.child
			    }
			    return tree;
	      	}
		},
		watch: {

			//	树形菜单过滤搜索用
	      	filterText(val) {
	        	this.$refs.tree.filter(val);
	      	}
		},
		props: [
			"activeClass"
		],
		created: function(){
			var _this = this;
			AJAX.get("/area-device",{}).then(function(e){
             	if(e.errCode == 0){
             		_this.treeDateTest = _this.treeFormat(e)['children'];
             	}
            }).catch(function(e){
             	_this.$message(e.errMsg || '请求异常，请稍后再试！');
            })
		}
	}
</script>
<style lang="scss">
	//	默认的树形菜单
	.default-tree{
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		&.hidden{
			display: none;
		}
		input{
			border: none;
			border-bottom: 1px solid #f2f2f2;
			width: 400px;
			padding: 10px;
			outline: none;
			margin: 0 0 0 10px;
		}
		.el-icon-close{
			position: absolute;
			right: 10px;
			top: 10px;
			font-size: 18px;
			color: #888888;
			cursor: pointer;
			&:hover{
				transform: scale(1.2);
				font-weight: bold;
				color: red;
			}
		}
		.default-backmood{
			position: fixed;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: rgba(0,0,0,.5);
			z-index: 5;
		}
		.tree-case{
			position: fixed;
			z-index: 6;
			width: 560px;
			height: 420px;
			border-radius: 5px;
			background: #ffffff;
			box-shadow: 0 0 20px #323232;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			overflow: hidden;
			.buttons{
				width: 100%;
				position: absolute;
				bottom: 0;
				left: 0;
				padding: 5px 0 15px;
				text-align: center; 
			}
			.el-tree{
			    height: 320px;
    			overflow-y: scroll;
			}
		}
	}
</style>



